.signup-button {
    background: none;
    border: 2px solid white;
    border-radius: 10px;
    font-size: 30px;
    color: white;
    padding: 18px 10px;
}

.icon-btn {
     background: none;
    border: 4px solid var(--blue);
    padding: 5px 15px;
    color: white;
    width: 100px;
    height: 100px;
    border-radius: 6px
}
.sm-icon-btn {
    background: none;
    border: 2px solid white;
    padding: 1px 8px;
    color: white;
    width: 40px;
    height: 40px;
    border-radius: 6px;
}

.borderless {
    border:none;
}

.button-container {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 768px) {
    .button-container {
        /* width: 180px; */
    }
}