.footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 120px 0px;
}

.footer-container > div {
    font-size: 90px;
    flex: 0 0 50%;
    font-weight: bold;
    display: flex;
    justify-content: center;
}

.footer-container > div > img {
    margin: auto;
    display: block;
    padding: 40px;
}

.mobile-footer {
    background: var(--blue);
    color: white;
    width:100%;
    font-size:12px;
    /* position: absolute; */
    /* bottom:0px; */
}

.mobile-footer-wrapper {
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    padding: 20px 10px;
}

.mobile-footer-wrapper svg {
    width:18px;
    height:18px;
}