.info-view {
    display:flex;
    justify-content: center;
    align-items: center;
     padding: 100px 0px 140px;
}

.info-view-wrapper {
    width: 90%;
}

.info-view-header {
    font-size:50px;
    font-weight:bold;
    text-align: center;
    font-style: italic;
}

.info-view-body {
    font-family: Avante Garde Std;
    font-size:30px;
    margin:60px 150px;
}

.info-view-body > div {
    margin:90px  0px;
}

.info-view-body > div:last-of-type {
    margin-bottom:0px;
}

@media (max-width: 768px) {
    .info-view {
           padding:30px 0px 70px;

    }
    .info-view-wrapper {
        width:80%;
    }
    .info-view-header {
        font-size: 31px;
        text-align: left;
    }
    .info-view-body {
        font-size: 21px;
        margin:0px;
    }
}