.faq {
    padding: 110px 0px;
    color: var(--blue);
    background: white;
}

.faq-line {
    display: flex;
    width: 90%;
    margin:70px 0px;
}

.faq-line > div {
    line-height: 40px;
    width: 50%;
}

.faq-line > div:nth-child(1) {
    font-family: Avante Garde Bold;
    font-size: 30px;
    text-align: right;
    padding-right: 10px;
}

.faq-line > div:nth-child(2) {
    font-size: 24px;
    text-align: left;
    padding-left: 10px;
    line-height: 40px;
}

@media (max-width: 768px) {
    .faq {
        padding: 50px 0px;
        justify-content: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .faq-line {
        display: block;
        width: 75%;
        margin: 20px 0px;
    }

    .faq-line > div {
        line-height: 25px;
        width: 100%;
    }

    .faq-line > div:nth-child(1) {
        font-size: 25px;
        font-weight: bold;
        text-align: left;
        margin-bottom: 4px;
    }

    .faq-line > div:nth-child(2) {
        font-size: 18px;
        text-align: left;
        padding-left: 10px;
        margin-left: 45px;
        width: 80%;
        line-height: 25px;
    }
}
