.hero-wrapper {
    padding: 180px;
}

.performer-name {
    font-size: 70px;
    font-weight: bold;
    font-style: italic;
    text-transform: uppercase;
    letter-spacing: 6px;
}

.hero-time {
    font-size: 40px;
    font-style: italic;
    letter-spacing: 4px;
    padding-left: 10px;
}

.hero-bottom {
    margin-left: 40px;
}

.small-cta {
    width: 155px;
    margin-left: 10px;
    font-size: 18px;
    /* margin-top: 20px; */
    margin-bottom: 60px;
}

.small-cta > div:first-child {
    margin-bottom: 6px;
}

.signup-wrapper {
    position: relative;
    margin: 30px 0px 20px;
}

.signup-wrapper.mobile {
    display: flex;
    justify-content: center;
}

.hero-footer {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    position: absolute;
    bottom: 0px;
}

.hero-footer div {
    margin-right: 10px;
}

.hero-footer .social-wrapper {
    display: flex;
}

.arrow {
    position: absolute;
}

@media (max-width: 768px) {
    .full-view.hero {
        /* background: ; */
    }
    .hero-wrapper {
        padding: 10px;
    }

    .performer-name {
        font-size: 50px;
        transform: rotate(-5deg);
        padding: 30px 10px 15px;
    }

    .hero-bottom {
        margin-left: 110px;
    }

    .small-cta {
        margin-top:20px;
    }

    .arrow {
        width: 2.7%;
        position: absolute;
        left: 100px;
        top: 71px;
    }
}
