
.rotated-header {
    position: absolute;
    left: 90px;
    top: 70px;
    font-size: 50px;
    transform-origin: center;
    transform: rotate(-5deg);
}.interact-view-body {
    align-self: center;
    /* justify-self: center; */
    text-align: center;
    width: 61%;
    font-size: 70px;
    font-weight: bold;
}
@media (max-width: 768px) {
    .interact {
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .rotated-header {
        position:unset;
    }
    .interact-view-body{
        font-size: 50px;
        width:90%;
    }
}

