@font-face {
    font-family: Avante Garde;
    src: url("./styles/fonts/itc-avant-garde-medium.ttf");
}

@font-face {
    font-family: Avante Garde Std;
    src: url("./styles/fonts/itc-avant-garde-std-bk.otf");
}

@font-face {
    font-family: Avante Garde Bold;
    src: url("./styles/fonts/itc-avant-garde-std-bold.otf");
}

:root {
    --blue: #283b99;
    --pink: #ff0ff8;
}

.card-theme {
    background: linear-gradient(180deg, var(--blue), black);
    color: white;
}

.card-theme-inverse {
    background: white;
    color: var(--blue);
}

.card-wrapper {
    padding: 50px 0px;
}

.App {
    font-family: Avante Garde;
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    color: white;
    overflow-x: hidden;
}

.header {
    display: flex;
    justify-content: space-between;
}

.full-view {
    /* background: red; */
    /* border: 1px solid white; */
    /* width: 100vw; */
    /* height: 90vh; */
    position: relative;
}

@media (max-width: 768px) {
    .full-view {
        /* height: 100vh; */
    }
}

.oversized-text {
    font-size: 100px;
}

.big-heading { 
    font-family: Avante Garde Bold;
    font-size: 90px;
    font-weight: bold;
    text-align: center;
    font-style: italic;
}

.centered-p {
    font-family: Avante Garde Bold;
    font-size: 35px;
    /* padding: 0px; */
    text-align: center;
}

@media (max-width: 768px) {
    .centered-p {
        font-size: 30px;
        text-align: center;
        width: 85%;
        padding: 24px;
        font-weight: bold;
        margin: auto;
        letter-spacing: 2px;
    }
    .big-heading {
        font-size: 34px;
    }
}

.button-wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

/* this might be too specific to the footer case */
.button-wrapper > div {
    margin-top:30px;
    flex: 0 0 28%;
}


.btn-secondary {
    border: 1px solid var(--blue);
    font-size: 50px;
    color: var(--blue);
    background: none;
    padding: 10px 20px;
    border-radius: 17px;
    font-family: "AVANTE GARDE";
}

.col-2 {
    display: flex;
    justify-content: center;
    align-items: center;
}
.col-2 > div {
    padding: 30px 0px;
    flex: 0 0 50%;
}

.col-wrapper {
    display: flex;
    justify-content: center;
}

.col-content {
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 1px;
    text-align: center;
    padding: 50px 0px 30px;
}

.col-content img {
    width: 100px;
    padding: 30px;
}
